.events{
    height: 100%;
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: 0;
}

.dark-events *{
    color: rgba(255,255,255,1);
}

.form-control:focus{
    color: rgba(0,0,0,1);
}

.dark-form-control:focus{
    color: rgba(255,255,255,1);
}

.input-group{
    padding: 0;
}

.input-group-text{
    background: transparent;
    border: none;
    outline: none;
}

.input-group-text:focus{
    box-shadow: none;
    outline: none;
}

#allEvents.custom-control-input~.custom-control-label{
    left: 0.35rem;
    bottom: 0.1rem;
}

#allEvents.custom-control-input~.custom-control-label::before{
    width: 1.1rem;
    height: 1.1rem;
    border: 1.3px solid rgba(150,150,150,0.7);
    border-radius: 50%;
    background: transparent;
}

#allEvents.custom-control-input:focus~.custom-control-label::before{
    box-shadow: none !important;
    outline: none;
}

#allEvents.custom-control-input~.custom-control-label::after{
    width: 1.1rem;
    height: 1.1rem;
}

.custom-checkbox .custom-control-input:checked~.custom-control-label::after {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='8' height='8' viewBox='0.5 0.5 7 7'%3e%3cpath fill='%23111' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26l2.974 2.99L8 2.193z'/%3e%3c/svg%3e");
}

.form-control{
    border: 0;
    background: none;
    box-shadow: none;
}

.form-control:focus{
    border: 0;
    background: none;
    box-shadow: none;
}

.eventTable{
    width: 100%;
    border-radius: 1em;
}

.eventTable .table{
    border-radius: 1em;
    padding: 0;
    margin: 0;
}

.eventTable .thead, .eventTable .tbody{
    padding-left: 0;
    padding-right: 0;
}
.eventTable .tr{
    margin-left: 0;
    margin-right: 0;
    margin-left: .6rem;
    margin-right: .6rem;
}

.eventTable .th, .eventTable .td {
    text-align: center;
    padding: 0;
}

.eventTable .th{
    font-weight: 600;
}

.table{
    overflow: hidden;
}

.tbody {
    position: relative;
    padding: 0 1.5em;
}

.tbody .tr{
    border-radius: .4rem;
}

/*.tbody .tr:nth-child(odd){
    background: rgba(0,0,0,0.1);
}*/

.tbody .tr:hover{
    background-image: linear-gradient(to right bottom, red,orange,yellow);;
}

/* width */
.tbody::-webkit-scrollbar {
    width: .5em;
}
  
  /* Track */
.tbody::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 1em;
}
   
  /* Handle */
.tbody::-webkit-scrollbar-thumb {
    background: rgba(150,150,150, .5); 
    border-radius: 1em;
}
  
  /* Handle on hover */
.tbody::-webkit-scrollbar-thumb:hover {
    background: rgba(150,150,150, 1); 
}

.addEvent {
    position: absolute;
    right: 5%;
    bottom: 3%;
}

.addEventBtn{
    text-align: center;
    background: none;
}

.addEventBtn:focus{
    box-shadow: none;
    outline: none;
}

@media only screen and (max-width: 576px) {
    /* width */
    .tbody::-webkit-scrollbar {
        width: 0;
    }
}